import React from 'react';
import { cn } from '@bem-react/classname';
import './SocialButton.scss';

const SocialButton = ({ name, link, icon }) => {
  const classItem = cn('SocialButton');

  return (
    <a 
      className={classItem()}
      href={link} 
      target="_blank"
      rel="noreferrer"
      title={name}>
        <span role="img" className={classItem('Icon')}>
          { icon }
        </span>
    </a>
  )
}

export default SocialButton;