import React from 'react';
import { cn } from '@bem-react/classname';
import './Main.scss';

const Main = ({ children }) => {
  const classItem = cn('Main');

  return (
    <main className={classItem()}>
      <div className={classItem('Wrapper')}>
        { children }
      </div>
    </main>
  )
}

export default Main;