import React from 'react';
import { cn } from '@bem-react/classname';
import ProfilePicture from '../ProfilePicture';
import Title from '../Title';
import Text from '../Text';
import ProfileSocialList from '../ProfileSocialList';


const ReactTypingEffect = React.lazy(() =>
  import("react-typing-effect")
)

import './Profile.scss';

const Profile = ({ links, picture, name, description }) => {
  const classItem = cn('Profile');

  return (
    <div className={classItem()}>
      <div className={classItem('Picture')}>
        <ProfilePicture {...picture} />
      </div>
      <div className={classItem('Name')}>
        <Title>{ name }</Title>
      </div>
      <div className={classItem('Description')}>
        <Text>
          
          { typeof window !== 'undefined' ? (
            <React.Suspense fallback={<div />}>
              <ReactTypingEffect text={description} speed={100} typingDelay={0} eraseSpeed={25} />
            </React.Suspense>
          ) : (description[0]) }
        </Text>
      </div>
      <div className={classItem('Link')}>
        <ProfileSocialList items={links} />
      </div>
    </div>
  ) 
}

export default Profile;